.settings-tooltip {
  position: relative;
}

.settings-tooltip__name {
  text-underline-position: under;
  text-decoration: underline dotted gray;
}

.settings-tooltip__hover {
  color: #000;
  white-space: nowrap;
  z-index: 1;
  background: #ffa;
  border: 1px solid #000;
  padding: 5px;
  display: none;
  position: absolute;
}

.settings-tooltip:hover .settings-tooltip__hover {
  display: block;
}

